.rounded-1px {
  border-radius: 1px;
}
.rounded-2px {
  border-radius: 2px;
}
.rounded-6px {
  border-radius: 6px;
}
.tt-n {
  text-transform: none;
}
.cursor-pointer {
  cursor: pointer;
}
.absolute-fill {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}
.flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}
.object-fit-img {
  object-fit: cover;
  height: 100%;
  width: 100%;
}
//   ,    
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.color-gray {
  color: var(--basic-gray);
}
.bg-gray {
  background-color: var(--basic-gray);
}
.bg-gray-light {
  background-color: var(--basic-gray-light);
}
.color-neon {
  color: var(--neon);
}
.bg-neon {
  background-color: var(--neon);
}
.bg-green {
  background-color: var(--basic-green);
}
.color-green {
  color: var(--basic-green);
}
.color-black {
  color: var(--basic-black);
}
